
import Home from './Home';
import './App.css';

import {HashRouter,Route} from 'react-router-dom';
function App() {
  return (
    
   <HashRouter basename="/">
    
       <div>

       <Route exact path="/">
            <Home/>
          </Route>
          <Route path="/Contact">

          </Route>
          <Route path="/internships">
              Internships
          </Route>
          <Route path="/projects">
            Projects
          </Route>
       </div>
         
        
   </HashRouter>
   
  );
}

export default App;
